:root {
  --primary: #74b6e0;
  --secondary: #fbfff2;
  --tertiary: #252422;
  --accent1: #0479c0;
  --accent2: #3a90cc;
  --black: #000000;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--primary);
  color: var(--secondary);
}
p,
a,
li,
button,
span {
  line-height: 150%;
  font-weight: 400;
  margin: 0;
  font-family: "Inter", sans-serif;
}
h1,
.h1 {
  font-size: 2rem;
}
h2,
.h2 {
  font-size: 2rem;
}
h3,
.h3 {
  font-size: 1.5rem;
}
h4,
.h4 {
  font-size: 1.125rem;
}
h5,
.h5 {
  font-size: 0.875rem;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6 {
  line-height: 125%;
  font-weight: 400;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
  color: var(--secondary);
}
ul {
  list-style: none;
}
.p1 {
  font-size: 1rem;
}
.p2 {
  font-size: 0.875rem;
}
.p3 {
  font-size: 0.75rem;
}
.bold {
  font-weight: 600;
}
.btn {
  border: none;
  cursor: pointer;
  border-radius: 4px;
  color: var(--secondary);
  padding: 0.125rem 1.25rem;
  box-shadow: inset 0px 15px 50px -50px var(--secondary),
    inset 15px 0px 50px -50px var(--secondary),
    inset -20px 0px 50px -50px var(--black),
    inset 0px -20px 50px -50px var(--black);
}
.btn:hover {
  opacity: 0.8;
  transition: 1000ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.btn-cta {
  background-color: var(--accent1);
}
.btn-form {
  background-color: var(--primary);
}

.content-wrapper {
  margin: 0 0.75rem;
}

@media screen and (min-width: 768px) {
  .content-wrapper {
    margin: 0 4rem;
  }
}
@media screen and (min-width: 1080px) {
  .content-wrapper {
    margin: 0 8rem;
  }
}

@media screen and (min-width: 1440px) {
  .content-wrapper {
    margin: 0 12rem;
  }
}
@media screen and (min-width: 1800px) {
  .content-wrapper {
    margin: 0 20rem;
  }
}
@media screen and (min-width: 2200px) {
  .content-wrapper {
    margin: 0 32rem;
  }
}
